.filterFixedContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 10px;
}

.filterFixed {
  background-color: #0000A3;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 123px;
  height: 36px;
  border-radius: 4px;
}

.filterFixed p {
  text-transform: uppercase;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 14.16px;
  margin-right: 5px;
}